import axios from 'axios';
import { req,reqFile } from './axiosFun';

/**
 * 
 **/
// -
export const PatientsList = (params) => { return req("post", "/api/Patient/",params) };

// -
export const PatientsDelete = (params) => { return axios.delete("/api/Goods/delete?ids=" + params + "&token=" + localStorage.getItem('logintoken')).then(res => res.data) };

//
export const RemindClockIn = (params) => { return req("put", "/api/ClockIn/remindClockIn/" + params) };

export const exportExcel = (params) => { return reqFile("post", "/api/ClockIn/exportExcel/",params) };

//ClockIn
export const GetPatientSetting = (params) => { return req("get", "/api/Patient/getSetting/" + params) };
//
export const SetPatientVideo = (params) => { return req("put", "/api/Patient/" + params.pid, params.tid) };
//ClockIn
export const GetClockInByOpenId = (params) => { return req("get", "/api/ClockIn/" + params) };
// -
export const Patientfeedback = (params) => { return req("put", "/api/ClockIn/feedback/"+params.id, '"' + params.content+'"') };

/**
 * 
 **/
// -
export const MachineList = (params) => { return req("post", "/api/Machine/list", params) };
// -
export const MachineSave = (params) => { return req("post", "/api/Machine/save", params) };
// -
export const MachineDelete = (params) => { return axios.delete("/api/Machine/delete?ids=" + params + "&token=" + localStorage.getItem('logintoken')).then(res => res.data) };

/**
 * 
 **/
// 
export const GetVideoesList = (params) => { return req("get", "/api/Video", params) };

// 
export const GetVideoById = (params) => { return req("get", "/api/Video/" + params) };

// 
export const GetVideoLinkById = (params) => { return req("get", "/api/Video/GetVideoUrl/" + params) };

// 
export const AddVideo = (params) => { return req("post", "/api/Video", params) };

// 
export const UpdateVideo = (params) => { return req("put", "/api/Video/" + params.id + "&name=" + params.name + "&describe=" + params.describe) };

export const DeleteVideo = (params) => { return req("delete", "/api/Video/" + params) };

export const UploadFile = (params) => { return req("post","/api/File/?fileName="+params) };




