/**
 *  
 */
<template>
  <div>
    <!--  -->
    <el-breadcrumb separator-class="el-icon-arrow-right">
      <el-breadcrumb-item :to="{ path: '/' }"></el-breadcrumb-item>
      <el-breadcrumb-item></el-breadcrumb-item>
    </el-breadcrumb>
    <!--  -->
    <el-form :inline="true" :model="formInline" class="user-search">
      <el-form-item label="">
        <el-input size="small" v-model="formInline.search" placeholder=""></el-input>
      </el-form-item>
      <el-form-item>
        <el-button size="small" type="primary" icon="el-icon-search" @click="search"></el-button>
      </el-form-item>
      <el-form-item>
        <el-button size="small" type="primary" icon="el-icon-add" @click="replyFormVisible=true"></el-button>
      </el-form-item>
    </el-form>
    <!---->
    <el-table size="small" id="ou" :data="listData" highlight-current-row v-loading="loading" border
      element-loading-text="" style="width: 100%;">
      <el-table-column align="center" type="selection" width="60">
      </el-table-column>
      <el-table-column sortable prop="name" label="" width="100">
      </el-table-column>
      <el-table-column sortable prop="type" label="" width="100">
      </el-table-column>
      <el-table-column sortable prop="editUser" label="" width="120">
      </el-table-column>
      <el-table-column sortable prop="createdAt" label="" width="150">
        <template slot-scope="scope">
          <div>{{scope.row.createdAt|timestampToTime}}</div>
        </template>
      </el-table-column>
      <el-table-column sortable prop="describe" label="" width="300">
      </el-table-column>
      <el-table-column align="center" label="" min-width="300">
        <template slot-scope="scope">
          <el-button size="mini" @click="handleEdit(scope.$index, scope.row)"></el-button>
          <el-button size="mini" type="danger" @click="deleteVideo(scope.$index, scope.row)">/</el-button>
        </template>
      </el-table-column>
    </el-table>
    <!--  -->
    <Pagination v-bind:child-msg="pageparm" @callFather="callFather"></Pagination>

    <!--  -->
    <el-dialog title="" :visible.sync="editFormVisible" width="30%" @click="closeVideoDialog"
      @open="openVideoDialog" append-to-body>
      <div>
        <video ref="videoPlay" width="100%" height="100%" :src="viewVideoUrl" autoPlay preload="auto" controls>
        </video>
      </div>
      <div slot="footer" class="dialog-footer">
        <el-button size="small" @click="closeVideoDialog"></el-button>
      </div>
    </el-dialog>
    <!--  -->
    <el-dialog v-loading="loading" title="" :visible.sync="replyFormVisible" width="30%" @click="closeDialog"
      append-to-body>
      <el-form :model="videoForm" :rules="rules">
        <el-form-item label="" prop="name" :label-width="formLabelWidth">
          <el-input v-model="videoForm.name" autocomplete="off"></el-input>
        </el-form-item>
        <el-form-item label="" prop="describe" :label-width="formLabelWidth">
          <el-input v-model="videoForm.describe" autocomplete="off"></el-input>
        </el-form-item>
        <el-form-item label="" :label-width="formLabelWidth">
          <el-upload class="upload-demo" ref="upload" accept=".mp4" :multiple="false" :show-file-list="ture"
            action="void" :http-request="customUpload" auto-upload :limit="1">
            <el-button size="small" type="primary"></el-button>
          </el-upload>
        </el-form-item>
      </el-form>
      <div slot="footer" class="dialog-footer">
        <el-button size="small" @click="closeDialog"></el-button>
        <el-button size="small" type="primary" :loading="loading" class="title" @click="submitForm('editForm')">
        </el-button>
      </div>
    </el-dialog>
  </div>
</template>

<script>
import { GetVideoesList, GetVideoById, AddVideo,DeleteVideo,UploadFile ,GetVideoLinkById} from '../../api/basisMg'
import Pagination from '../../components/Pagination'

export default {
  data() {
    return {
      nshow: true, //switch
      fshow: false, //switch
      loading: false, //
      editFormVisible: false, //
      replyFormVisible:false,
      title: '',
      drawer: false,
      marks:['2022-07-01','2022-07-02','2022-07-03'],
      editForm: {
        deptId: '',
        deptName: '',
        deptNo: '',
        token: localStorage.getItem('logintoken')
      },
      videoForm:{},
      uploadFileLink:{},
      viewVideoUrl:'',
      // rules
      rules: {
        name: [
          { required: true, message: '', trigger: 'blur' }
        ],
        describe: [{ required: true, message: '', trigger: 'blur' }]
      },
      formInline: {
        pageIndex: 1,
        pageSize: 10,
        search: ''
      },
      // 
      seletedata: {
        ids: '',
        token: localStorage.getItem('logintoken')
      },
      userparm: [], //
      listData: [], //
      // 
      pageparm: {
        currentPage: 1,
        pageSize: 10,
        total: 10
      }
    }
  },
  // 
  components: {
    Pagination
  },
  /**
   * 
   */

  /**
   * 
   */
  created() {
    this.getdata(this.formInline)
  },

  /**
   * 
   */
  methods: {
    // 
    getdata(parameter) {
      this.loading = true
      GetVideoesList(parameter)
        .then(res => {
          this.loading = false
          if (res.data.code <=0) {
            this.$message({
              type: 'info',
              message: res.msg
            })
          } else {
            this.listData = res.data.data.list
            // 
            this.pageparm.currentPage = this.formInline.pageIndex
            this.pageparm.pageSize = this.formInline.pageSize
            this.pageparm.total = res.data.data.count
          }
        }).catch(err => {
          this.loading = false
          this.$message.error('')
        })
    },
    // 
    callFather(parm) {
      this.formInline.pageIndex = parm.currentPage
      this.formInline.pageSize = parm.pageSize
      this.getdata(this.formInline)
    },
    // 
    search() {
      this.getdata(this.formInline)
    },
    //
    handleEdit: function (index, row) {
      this.viewVideoUrl = ''//
      this.loading = true;
      const that = this;
      GetVideoLinkById(row.id).then(res => {
        if (res.data.code > 0) {
          console.log("" + res.data.data)
          this.viewVideoUrl = res.data.data
          this.editFormVisible = true
        } else {
          this.$message({
            type: 'info',
            message: res.data.message
          })
        }
      }).catch(err => {
        this.$message.error(err);
        this.loading = false;
      });
    },
    //
    remindClock : function(index, row) {
       this.$message({
            type: 'success',
            message: ''
          })
    },
    // 
    async submitForm(editData) {
       const parms = this.videoForm;
       const file = this.$refs.upload._data.uploadFiles[0].raw;
       this.loading = true;
       const fileId = await this.uploadFile(this.uploadFileLink, file, file.name);
       if (!fileId) {
         this.$message.error('!---');
         return;
       }
       parms.fileId = fileId
       parms.type = '' //01
       AddVideo(parms).then(res => {
         this.loading = false;
         if (res.data.code > 0) {
           this.$message.success('!');
         } else {
           this.$message.error('!');
         }
         this.closeDialog();
         this.videoForm = {};
         this.getdata(this.formInline);
       }).catch(err=>{
        this.$message.error(err);
        this.loading = false;
       })

    },
    customUpload(file) {
      this.loading = true
      UploadFile(file.file.name).then(res => {
        if (res.data.code >0) {
          this.uploadFileLink = res.data.data
        } else {
          this.$message.error('!');
        }
        this.loading = false
      })
    },
    // 
    deleteVideo(index, row) {
      this.$confirm('?', '', {
        confirmButtonText: '',
        cancelButtonText: '',
        type: 'warning'
      })
        .then(() => {
          DeleteVideo(row.id)
            .then(res => {
              if (res.data.code > 0) {
                this.$message({
                  type: 'success',
                  message: '!'
                })
                this.getdata(this.formInline)
              } else {
                this.$message({
                  type: 'info',
                  message: res.msg
                })
              }
            })
            .catch(err => {
              this.loading = false
              this.$message.error('')
            })
        })
        .catch(() => {
          this.$message({
            type: 'info',
            message: ''
          })
        })
    },
    // 
    closeDialog() {
      this.loading = false
      this.editFormVisible = false
      this.replyFormVisible = false
    },
    // 
    closeVideoDialog(){
      this.$refs.videoPlay.pause();
      this.closeDialog();
    },
    openVideoDialog(){

    },
    async uploadFile(result, file, filePath) {
      const data = new FormData();
      data.append("key", result.key);
      data.append("Signature", result.authorization);
      data.append("x-cos-security-token", result.token);
      data.append("x-cos-meta-fileid", result.cos_file_id);
      data.append("file", file, filePath);
      const fileraw = await this.request(result.url, 'POST', data)
      if (fileraw != '') {
        return '';
      }
      return result.fileid
    },
    request(url, method = 'GET', data = null) {
      return new Promise(function (resolve, reject) {
        const xhr = new XMLHttpRequest();
        xhr.withCredentials = true;
        xhr.addEventListener("readystatechange", function () {
          if (this.readyState === 4) {
            resolve(this.responseText)
          }
        });
        xhr.open(method, url);
        xhr.send(data);
      })
    }
  }
}
</script>

<style scoped>
.user-search {
  margin-top: 20px;
}
.userRole {
  width: 100%;
}
</style>





