// 
import Vue from 'vue';
import Router from 'vue-router';
// 
import login from '@/views/login';
// 
import index from '@/views/index';
/**
 * 
 */
// 
import Patient from '@/views/baseManage/Patient';
// 
import VideoManage from '@/views/baseManage/VideoManage';
/**
 * 
 */
// 
import Order from '@/views/pay/Order';
/**
 * 
 */
// 
import user from '@/views/system/user';
// 
import Module from '@/views/system/Module';
// 
import Role from '@/views/system/Role';
// 
import Dept from '@/views/system/Dept';
// 
import Variable from '@/views/system/Variable';
// 
import Template from '@/views/system/TemplateConfig'
// 
import Permission from '@/views/system/Permission';

/**
 * 
 */
// 
import druidLogin from '@/views/druid/login';


// 
Vue.use(Router);

// 
export default new Router({
  routes: [{
    path: '/',
    name: '',
    component: login,
    hidden: true,
    meta: {
      requireAuth: false
    }
  }, {
    path: '/login',
    name: '',
    component: login,
    hidden: true,
    meta: {
      requireAuth: false
    }
  }, {
    path: '/index',
    name: '',
    component: index,
    iconCls: 'el-icon-tickets',
    children: [{
      path: '/baseManage/Patient',
      name: '',
      component: Patient,
      meta: {
        requireAuth: true
      }
    }, {
      path: '/baseManage/VideoManage',
      name: '',
      component: VideoManage,
      meta: {
        requireAuth: true
      }
    }, {
      path: '/pay/Order',
      name: '',
      component: Order,
      meta: {
        requireAuth: true
      }
    }, {
      path: '/system/user',
      name: '',
      component: user,
      meta: {
        requireAuth: true
      }
    }, {
      path: '/system/Module',
      name: '',
      component: Module,
      meta: {
        requireAuth: true
      }
    }, {
      path: '/system/Role',
      name: '',
      component: Role,
      meta: {
        requireAuth: true
      }
    }, {
      path: '/system/Dept',
      name: '',
      component: Dept,
      meta: {
        requireAuth: true
      }
    }, {
      path: '/system/Variable',
      name: '',
      component: Variable,
      meta: {
        requireAuth: true
      }
    }, {
      path: '/system/Template',
      name: '',
      component: Template,
      meta: {
        requireAuth: true
      }
    },{
      path: '/system/Permission',
      name: '',
      component: Permission,
      meta: {
        requireAuth: true
      }
    }, {
      path: '/druid/login',
      name: '',
      component: druidLogin,
      meta: {
        requireAuth: true
      }
    }]
  }]
})



