/**
 *  
 */
<template>
  <div>
    <!--  -->
    <el-breadcrumb separator-class="el-icon-arrow-right">
      <el-breadcrumb-item :to="{ path: '/' }"></el-breadcrumb-item>
      <el-breadcrumb-item></el-breadcrumb-item>
    </el-breadcrumb>
    <!--  -->
    <el-form :inline="true" :model="formInline" class="user-search">
      <el-form-item label="">
        <el-input size="small" v-model="formInline.deptName" placeholder=""></el-input>
      </el-form-item>
      <el-form-item>
      <el-button size="small" type="primary" icon="el-icon-search" @click="search"></el-button>
      <el-date-picker v-model="daterange" type="daterange" align="right" start-placeholder="" end-placeholder=""
        :default-time="['12:00:00', '23:59:59']">
      </el-date-picker>
        <el-button size="small" type="primary" icon="el-icon-document" @click="exportExcel">Excel</el-button>
      </el-form-item>
    </el-form>
    <!---->
    <el-table size="small" id="ou" :data="listData" highlight-current-row v-loading="loading" border
      element-loading-text="" style="width: 100%;">
      <el-table-column align="center" type="selection" width="60">
      </el-table-column>
      <el-table-column sortable prop="name" label="" width="100">
      </el-table-column>
      <el-table-column sortable prop="sex" label="" width="100">
      </el-table-column>
      <el-table-column sortable prop="createdAt" label="" width="150">
        <template slot-scope="scope">
          <div>{{scope.row.createdAt|timestampToTime}}</div>
        </template>
      </el-table-column>
      <el-table-column sortable prop="sameDayIsCheck" align="center" label="" width="120">
      </el-table-column>
      <el-table-column sortable label="" width="120">
        <template slot-scope="scope">
          <el-button @click="openCalendar(scope.row)" size="mini" type="primary" style="margin-left: 16px;">
          </el-button>
        </template>
      </el-table-column>
      <el-table-column sortable prop="teachName" label="" width="120">
      </el-table-column>
      <el-table-column align="center" label="" min-width="300">
        <template slot-scope="scope">
          <el-button size="mini" @click="remindClock(scope.$index, scope.row)"></el-button>
          <el-button size="mini" @click="handleEdit(scope.$index, scope.row)"></el-button>
          <el-button size="mini" type="danger" @click="openSetTeachingVideo(scope.$index, scope.row)"></el-button>
        </template>
      </el-table-column>
    </el-table>
    <!--  -->
    <Pagination v-bind:child-msg="pageparm" @callFather="callFather"></Pagination>

    <el-drawer ref="refdrawer" :visible.sync="drawer" size="35%" :with-header="false">
      <span style="color:red;margin:15px 15px"></span>
      <el-calendar>
        <!--  2.5 slot  2.6 slot  data.day yyyy-MM-dd-->
        <template slot="dateCell" slot-scope="{date, data}">
          <p :class="data.isSelected ? 'is-selected' : ''">
            {{ data.day.split('-').slice(1).join('-') }}
          </p>
          <div v-for="(item,index) in marks" :key="index" style="width:0px;height:0px">
            <el-tag v-if="data.day == item.date" size="mini" :disable-transitions="false"
              @click="handleEdit('',{todayClockInId:item.id,lastCheckInVideos:item.videos})">
            </el-tag>
          </div>
        </template>
      </el-calendar>
    </el-drawer>
    <!--  -->
    <el-dialog :title="videoTitle" :visible.sync="editFormVisible" width="30%" @click="closeVideoDialog"
      @open="openVideoDialog" append-to-body>
      <div>
        <video ref="videoPlay" width="100%" height="100%" :src="viewVideoUrl" autoPlay preload="auto" controls>
        </video>
      </div>
      <div>
        <el-button-group>
          <el-button v-for="btn in currentDayVideos" :key="btn.id" type="danger" @click="changeVideo(btn.id)" >
           {{btn.name}}
          </el-button>
      </el-button-group>
      </div>
      <div>
        <el-form :model="editForm">
          <el-form-item label="" label-width="80">
            <el-input v-model="editForm.replyContent" autocomplete="off"></el-input>
          </el-form-item>
        </el-form>
      </div>
      <div slot="footer" class="dialog-footer">
        <el-button size="small" type="primary" :loading="loading" class="title" @click="submitForm('editForm')">
        </el-button>
        <el-button size="small" @click="closeVideoDialog"></el-button>
      </div>
    </el-dialog>
    <!--  -->
    <el-dialog title="" :visible.sync="settingFormVisible" width="30%" @click="closeDialog" append-to-body>
      <el-form>
        <el-form-item label="" prop="roles">
          <el-select size="small" v-model="settingVideo.tid" multiple placeholder="">
            <el-option v-for="item in settingVideoOptions" :key="item.id" :label="item.name" :value="item.id">
            </el-option>
          </el-select>
        </el-form-item>
      </el-form>
      <el-timeline reverse="true">
        <el-timeline-item v-for="(activity, index) in activities" :key="index" :timestamp="activity.timestamp">
          {{activity.content}}
        </el-timeline-item>
      </el-timeline>
      <div slot="footer" class="dialog-footer">
        <el-button size="small" @click="closeDialog"></el-button>
        <el-button size="small" type="primary" :loading="loading" class="title" @click="settingTeachingVideo()">
        </el-button>
      </div>
    </el-dialog>

  </div>
</template>

<script>
import { PatientsList, GetVideoLinkById, GetVideoesList,
  GetClockInByOpenId, GetPatientSetting, SetPatientVideo,
  Patientfeedback,RemindClockIn,exportExcel } from '../../api/basisMg'
import Pagination from '../../components/Pagination'

export default {
  data() {
    return {
      nshow: true, //switch
      fshow: false, //switch
      loading: false, //
      editFormVisible: false, //
      settingFormVisible:false,//
      settingVideo:{pid:'',tid:''},
      settingVideoOptions:[],
      viewVideoUrl:'',
      replyFormVisible:false,
      videoTitle:'',
      title: '',
      drawer: false,
      todayClockInId:'',//id
      marks:[],
      editForm: {
        replyContent: ''
      },
      currentDayVideos:[],
      activities: [],
      form:{},
      daterange:'',
      // rules
      rules: {
        deptName: [
          { required: true, message: '', trigger: 'blur' }
        ],
        deptNo: [{ required: true, message: '', trigger: 'blur' }]
      },
      formInline: {
        pageIndex: 1,
        pageSize: 10,
        search: '',
        token: localStorage.getItem('logintoken')
      },
      // 
      seletedata: {
        ids: '',
        token: localStorage.getItem('logintoken')
      },
      userparm: [], //
      listData: [], //
      // 
      pageparm: {
        currentPage: 1,
        pageSize: 10,
        total: 10
      }
    }
  },
  // 
  components: {
    Pagination
  },
  /**
   * 
   */

  /**
   * 
   */
  created() {
    this.getdata(this.formInline)
  },

  /**
   * 
   */
  methods: {
    // 
    getdata(parameter) {
      this.loading = true
      /***
       *  
       */
      PatientsList(parameter)
        .then(res => {
          this.loading = false
          if (res.data.code <0) {
            this.$message({
              type: 'info',
              message: res.data.message
            })
          } else {
            this.listData = res.data.data.list
            // 
            this.pageparm.currentPage = this.formInline.pageIndex
            this.pageparm.pageSize = this.formInline.pageSize
            this.pageparm.total = res.data.count
          }
        })
        .catch(err => {
          this.loading = false
          this.$message.error('')
        })
    },
    // 
    callFather(parm) {
      this.formInline.page = parm.currentPage
      this.formInline.limit = parm.pageSize
      this.getdata(this.formInline)
    },
    // 
    search() {
      this.getdata(this.formInline)
    },
    //
    handleEdit: function (index, row) {
      if(row.lastCheckInVideos==null || row.lastCheckInVideos.length == 0){
        this.$message({
            type: 'info',
            message: ''
          })
          return
      }
      this.viewVideoUrl = ''//
      this.loading = true;
      const that = this;
      GetVideoLinkById(row.lastCheckInVideos[0].id).then(res => {
        if (res.data.code > 0) {
          console.log("" + res.data.data)
          this.viewVideoUrl = res.data.data
          this.todayClockInId = row.todayClockInId
          this.currentDayVideos = row.lastCheckInVideos
          this.editFormVisible = true
        } else {
          this.$message({
            type: 'info',
            message: res.data.message
          })
        }
        this.loading = false;
      }).catch(err => {
        this.$message.error(err);
        this.loading = false;
      });
    },
    changeVideo(id){
      GetVideoLinkById(id).then(res => {
        if (res.data.code > 0) {
          console.log("" + res.data.data)
          this.viewVideoUrl = res.data.data
        } else {
          this.$message({
            type: 'info',
            message: res.data.message
          })
        }
        this.loading = false;
      }).catch(err => {
        this.$message.error(err);
        this.loading = false;
      });
    },

    //
    remindClock : function(index, row) {
      RemindClockIn(row.openId)
      .then(res => {
        if (res.data.code > 0) {
          this.$message({
            type: 'success',
            message: ''
          })
        } else {
          this.$message({
            type: 'success',
            message: res.data.message
          })
        }
      })
        .catch(err => {
          this.loading = false
          this.$message.error('')
        })
    },
  //
    openSetTeachingVideo: function (index, row) {
      this.settingVideo.pid = row.id
      this.settingFormVisible = true
      GetPatientSetting(this.settingVideo.pid)
      .then(res => {
          if (res.data.code > 0) {
            this.activities = res.data.data.activities
            this.settingVideo.tid = res.data.data.setTeachVideos.map(o=>o.id)
          } else {
            this.$message({
              type: 'info',
              message: res.data.message
            })
          }
        })
      .catch(err => {
          this.loading = false
          this.$message.error('')
        })

      GetVideoesList({
        pageIndex: 1,
        pageSize: 50,
        search: ''
      })
      .then(res => {
          if (res.data.code > 0) {
            this.settingVideoOptions = res.data.data.list
          } else {
            this.$message({
              type: 'info',
              message: res.data.message
            })
          }
        })
        .catch(err => {
          this.loading = false
          this.$message.error('')
        })
    },
    // 
    settingTeachingVideo() {
      SetPatientVideo({ pid: this.settingVideo.pid, tid: this.settingVideo.tid })
        .then(res => {
          if (res.data.code > 0) {
            this.settingVideo.pid = ''
            this.settingFormVisible = false
          } else {
            this.$message({
              type: 'info',
              message: res.data.message
            })
          }
        })
        .catch(err => {

          this.loading = false
          this.$message.error('')
        })
    },
    // 
    submitForm() {
      Patientfeedback({
        id: this.todayClockInId,
        content: this.editForm.replyContent
      })
        .then(res => {
          this.loading = false
          this.$message({
              type: 'success',
              message: res.data.message
          })
          this.closeVideoDialog()
        })
        .catch(err => {
          this.loading = false
          this.$message.error('')
        })
    },
    // 
    deleteUser(index, row) {
      this.$confirm('?', '', {
        confirmButtonText: '',
        cancelButtonText: '',
        type: 'warning'
      })
        .then(() => {
          deptDelete(row.deptId)
            .then(res => {
              if (res.success) {
                this.$message({
                  type: 'success',
                  message: '!'
                })
                this.getdata(this.formInline)
              } else {
                this.$message({
                  type: 'info',
                  message: res.msg
                })
              }
            })
            .catch(err => {
              this.loading = false
              this.$message.error('')
            })
        })
        .catch(() => {
          this.$message({
            type: 'info',
            message: ''
          })
        })
    },
    // 
    closeDialog() {
      this.loading = false
      this.editFormVisible = false
      this.replyFormVisible = false
      this.settingFormVisible =false
    },
    // 
    closeVideoDialog(){
      this.$refs.videoPlay.pause();
      this.closeDialog();
    },
    openVideoDialog(){

    },
    openCalendar(row){
      this.drawer = true
      //
      GetClockInByOpenId(row.openId).then(res => {
        if (res.data.code > 0) {
         this.marks = res.data.data
        } else {
          this.$message({
            type: 'info',
            message: res.data.message
          })
        }
      }).catch(err => {
        this.$message.error(err);
      });


    },
    exportExcel() {

      const pamams = {
        date1:this.fermitTime(this.daterange[0]),
        date2:this.fermitTime(this.daterange[1])
      }
      exportExcel(pamams)
      .then(res => {
        try {
          this.$FileSaver.saveAs(res.data,
            "sheetjs.xls"
          );
        } catch (e) {
          if (typeof console !== "undefined") console.log(e, table_write);
        }
        if (res.data.code < 0) {
          this.$message({
            type: 'info',
            message: res.data.message
          })
        }
      }).catch(err => {
        this.$message.error(err);
      });
    },
    fermitTime(time) {
      var now = new Date(time);
      var year = now.getFullYear();
      var mon = now.getMonth() + 1;
      var date = now.getDate();
      if (mon < 10) {
        mon = '0'+mon;
      };
      if (date < 10) {
        date = '0'+date;
      }
      var postDate = year +'-'+mon+'-'+date;
      return postDate;
    }
  }
}
</script>

<style scoped>
.user-search {
  margin-top: 20px;
}
.userRole {
  width: 100%;
}
</style>





